<svelte:options tag="hand-jobs" />

<script>
    import { onMount } from "svelte"
    import { http } from "../utils/http"

    $: data = []
    $: jobs = []
    $: companies = []
    $: filter = {
        company: "",
        type: "",
    }
    $: showDetails = false

    onMount(async () => {
        data = await http.get(`jobs`).json()
        jobs = data

        jobs.forEach((job) => {
            if (!companies.find((company) => company.id === job.company.id)) {
                companies = [...companies, job.company]
            }
        })
    })

    function filterJobs() {
        jobs = data.filter((job) => {
            if (filter.company && job.company.id != filter.company) return false
            if (filter.type && job.type != filter.type) return false
            return true
        })
    }

    function toggleDetails(id) {
        if (showDetails === id) {
            showDetails = false
        } else {
            showDetails = id
        }
    }
</script>

<link
    href="https://fonts.googleapis.com/css?family=Montserrat"
    rel="stylesheet"
/>
<link
    href="https://cdn.jsdelivr.net/npm/bootstrap@5.1.3/dist/css/bootstrap.min.css"
    rel="stylesheet"
    integrity="sha384-1BmE4kWBq78iYhFldvKuhfTAU6auU8tT94WrHftjDbrCEXSU1oBoqyl2QvZ6jIW3"
    crossorigin="anonymous"
/>

<main>
    <div class="header row justify-content-center">
        <div class="col-md-4">
            <label for="company-select">Unternehmen</label>
            <select
                id="company-select"
                bind:value={filter.company}
                on:change={filterJobs}
            >
                <option value="">Alle</option>
                {#each companies as company}
                    <option value={company.id}>{company.shortname}</option>
                {/each}
            </select>
        </div>
        <div class="col-md-4">
            <label for="type-select">Stellenart</label>
            <select
                id="type-select"
                bind:value={filter.type}
                on:change={filterJobs}
            >
                <option value="">Alle</option>
                <option value="Vollzeit">Vollzeit</option>
                <option value="Teilzeit">Teilzeit</option>
                <option value="Ausbildung">Ausbildung</option>
                <option value="Minijob">Minijob</option>
            </select>
        </div>
    </div>

    <table class="table">
        <thead>
            <tr>
                <th>Bezeichnung</th>
                <th>Stellenart</th>
                <th>Unternehmen</th>
                <th />
            </tr>
        </thead>
        <tbody>
            {#each jobs as job}
                <tr class={showDetails === job.id ? "expanded" : ""}>
                    <td style="padding-left: 1em">
                        <strong>
                            {job.title} (m/w/d)
                        </strong>
                    </td>
                    <td>{job.type}</td>
                    <td>
                        <a
                            href={job.company.website
                                ? job.company.website
                                : "#"}
                            target="_blank"
                        >
                            {job.company.shortname}
                        </a>
                    </td>
                    <td>
                        <button
                            type="button"
                            class="btn btn-link pt-0 pb-0"
                            on:click={() => toggleDetails(job.id)}
                        >
                            ❯
                        </button>
                    </td>
                </tr>
                {#if showDetails === job.id}
                    <tr
                        class={showDetails === job.id
                            ? "details-row expanded"
                            : "details-row"}
                    >
                        <td colspan="12">
                            <h3>Beschreibung</h3>
                            {job.description}
                            <br />
                            <br />
                            <h3>Deine Aufgaben</h3>
                            <pre>{job.todos}</pre>
                            <h3>Das kannst du erwarten</h3>
                            <pre>{job.benefits}</pre>
                            <h3>Das solltest du mitbringen</h3>
                            <pre>{job.requirements}</pre>
                        </td>
                    </tr>
                {/if}
            {/each}
        </tbody>
    </table>
</main>

